*, :before, :after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  -webkit-font-smoothing: antialiased;
  line-height: 1.5;
}

img, picture, video, canvas, svg {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

#root, #__next {
  isolation: isolate;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  align-items: center;
  gap: 40px 30px;
  margin-top: 30px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.people .person {
  transition: all 1s ease-in-out;
  position: relative;
}

.people .person:before {
  content: "";
  z-index: -1;
  background: none;
  width: 100%;
  height: 100%;
  position: absolute;
  top: -10px;
  left: 0;
}

.people .person .photo {
  justify-content: center;
  margin-bottom: 8px;
  display: flex;
  position: relative;
}

.people .person .photo .rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 200px;
  align-content: center;
  width: 32px;
  height: 32px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  position: absolute;
  top: 84px;
  right: 10px;
}

.people .person img {
  border-radius: 200px;
  width: 200px;
  transition: border-radius 1s ease-in-out;
}

.people .person .details {
  color: #2f4f4f;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 700;
}

.people .person .details .extra {
  color: #000;
  font-size: 18px;
  font-weight: 400;
  display: none;
}

.people .person .details .extra .sport:after {
  content: ",";
}

.people .person .details .extra .pay:before {
  content: "$";
}

.people .person .details .extra .pay:after {
  content: "M";
}

.people .person:hover {
  z-index: 1;
  transform: scale(1.2);
}

.people .person:hover img {
  border-radius: 60px;
}

.people .person:hover .details .extra {
  z-index: 1;
  white-space: nowrap;
  display: block;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.people .person:hover:hover:before {
  background: #eee;
  border-radius: 10px;
  width: 220px;
  height: 287px;
  left: 50%;
  transform: translateX(-50%);
}
